.footer {
    background: #193b1e;
    margin: 0;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
}
.socials {
    height: 58px;
    width: 58px;
}
.socials:hover {
    transform: scale(1.2);
}
.bottomSocials {
    list-style: none;
    display: flex;
    justify-content: center;
    margin: 0;
    padding: 1rem 1rem 0;
}
.bottomSocials li {
    padding: .5rem;
}
.container {
    display: flex;
    justify-content: space-between;
}
.left {
    text-align: left;
    margin-bottom: .25rem;
    margin-left: .25rem;
    color: #e4f0df;
    font-family:Figtree,sans-serif;
}
.right {
    text-align: right;
    margin-bottom: .25rem;
    margin-right: .25rem;
    color: #e4f0df;
    font-family:Figtree,sans-serif;
}
.link {
    color: #e4f0df;
}
.link2 {
    color: #e4f0df;
    letter-spacing: .08rem;
    font-family:Figtree,sans-serif;
    margin-bottom: 2rem;
    font-size: 1.25rem;
    text-decoration: none;
    margin: 0 auto 2rem;
    text-align: center;
}
.link2:hover {
    transform: scale(1.2);
    color: #ce4760;
}