body {
    margin: 0;
    background: #193b1e;
}
html {
    font-size: 100%;
}
.topContainer {
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: center;
}
.articleContainer {
    display: flex;
    justify-content: space-around;
    margin-bottom: 5rem;
}
.articleImages {
    height: 250px;
    width: 250px;
    border-radius: 200px;
}
.articleImages:hover {
    transform: scale(1.05);
    box-shadow: 0 0 15px #000;
    border: 1px solid #000;
    box-sizing: border-box;
}
.articleWords {
    color: #572cb5;
    font-family:'indie flower';
    font-weight: bold;
    letter-spacing: .15rem;
    font-size: 1.25rem;
    width: 75%;
}
.articleTitle {
    display: flex;
    justify-content: center;
}
.topBox {
    margin: 3%;
    background-position: top;
    background-image: url(../public/logo11.svg);
    background-color: #e4f0df;
    background-blend-mode: screen;
    border-radius: 25px;
    padding: 3%;
}
.h1 {
    color: #193b1e;
    font-family:'Prata',serif;
    text-align: left;
    letter-spacing: .3rem;
    margin: 0 .25rem;
    box-sizing: border-box;
    border-bottom: 2px #193b1e dashed;
    padding-bottom: 1rem;
}
.p {
    color: #193b1e;
    font-family:Figtree,sans-serif;
    font-size: 1.5rem;
    letter-spacing: .05rem;
    width: 90%;
    margin: 3rem auto 5rem;
    line-height: 3rem;
    text-align: right;
    font-weight: bold;
}
.banner {
    min-height: 35vh;
    background-image: url(../public/logo11.svg);
    background-color: #e4f0df;
    background-blend-mode: overlay;
    padding: .5rem;
    box-sizing: border-box;
    background-position: top;
}
@media only screen and (max-width:1100px){
    .articleContainer {
        flex-direction: column;
    }
    .articleImages {
        height: 300px;
        width: 300px;
        margin-top: 3rem;
    }
    .p {
        text-align: center;
    }
}