.topContainer {
    background-position: top;
    background-image: url(../public/logo11.svg);
    background-color: #e4f0df;
    background-blend-mode: screen;
    margin: 3%;
    padding: 3%;
    border-radius: 25px;
}
.banner {
    min-height: 35vh;
    background-image: url(../public/logo11.svg);
    background-color: #e4f0df;
    padding: .5rem;
    box-sizing: border-box;
    background-position: top;
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin-bottom: 3.7rem;
    position: relative;
    z-index: -1000;
    background-blend-mode: overlay;
}
.banner::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-image: url('../public/EELogo.svg');
    background-size: contain;
    background-position: center;
    opacity: .1;
    z-index: -900;
    background-repeat: no-repeat;
}
.h1 {
    color: #e4f0df;
    font-family:'Great vibes',cursive;
    text-align: right;
    letter-spacing: .3rem;
    margin: 0 .25rem;
    box-sizing: border-box;
    border-bottom: 4px #e4f0df double;
    padding-bottom: 1rem;
}
.wordContainer {
    margin: 3%;
    padding: 3%;
}
.h2 {
    color: #193b1e;
    font-family: Prata,serif;
    letter-spacing: .1rem;
    font-size: 2rem;
    text-align: left;
    border-bottom: dashed 1px #193b1e;
    padding-bottom: 2rem;
    margin-top: 0;
}
.p {
    color: #193b1e;
    font-family:Figtree,sans-serif;
    font-size: 1.25rem;
    text-align: left;
}
.shippingP {
    color: #193b1e;
    font-family:Figtree,sans-serif;
    font-size: 1.25rem;
    text-align: right;
    line-height: 2rem;
}
.topP {
    color: #e4f0df;
    text-align: right;
    font-family:Figtree,sans-serif;
    line-height: 2rem;
    margin: 1rem .25rem 0;
    font-size: 1.1rem;
}
.boxContainer {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    margin: 3rem 0;
}
.imgBox {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 300px;
}
.imgBox1 {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 300px;
}
.img {
    width: 200px;
    height: 200px;
    border-radius: 25px;
}
.imgP {
    margin: .5rem 0;
    padding: 5px;
    text-align: center;
    width: 200px;
    color: #193b1e;
    font-family:Figtree,sans-serif;
    letter-spacing: .05rem;
    font-weight: bold;
}
.img:hover {
    transform: scale(2);
    box-shadow: 3px 3px 15px #000;
}
.returnLink {
    color: #572cb5;
    text-decoration: none;
}
.returnLink:hover {
    color: #ce4760;
}
.topLinks {
    color: #e4f0df;
    text-underline-offset: 2.25px;
}
.topLinks:hover {
    color: #ce4760;
}
@media only screen and (max-width:800px){
    .boxContainer {
        flex-direction: column;
    }
    .imgBox {
        margin-bottom: 5rem;
    }
    .imgBox1 {
        margin-bottom: 0;
    }
}
  
  
  