.topContainer {
    display: flex;
    justify-content: center;
    margin-top: 5rem;
}
.title {
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: right;
    margin-right: 5%;
}
.titleContainer {
    display: flex;
    flex-direction: row-reverse;
}
.author {
    border-right: 1px solid #fffceb;
    border-bottom: 1px solid #fffceb;
    padding: 2%;
    color: #fffceb;
    font-family:'Indie flower',sans-serif;
    letter-spacing: .25rem;
    font-weight: bold;
    font-size: 1.25rem;
    margin-top: 2rem;
}
.blockquote {
    color: #fffceb;
    font-family:Figtree,sans-serif;
    letter-spacing: .15rem;
    border-bottom:  3px double #fffceb;
    border-right: double 3px #fffceb;
    padding-bottom: .5rem;
    padding-right: .5rem;
}
.info {
    display: flex;
    justify-content: center;
    flex-direction: column;
}
.h1 {
    margin: 0;
    font-family:Prata,serif;
    color: #fffceb;
    letter-spacing: .15rem;
    padding-left: 2%;
    margin-right: 2%;
    font-size: 3rem;
}
.img {
    height: 300px;
    width: 300px;
    border-radius: 150px;
}
.articleContainer {
    background: #e4f0df;
    border: double 10px #193b1e;
    margin: 5%;
    padding: 1rem;
}
.p1 {
    color: #193b1e;
    font-family:Figtree,sans-serif;
    text-align: left;
    line-height: 2.5rem;
    width: 90%;
    font-size: 2rem;
    margin-top: 2rem;
    margin-bottom: 6rem;
    letter-spacing: .05rem;
}
.p2 {
    color: #193b1e;
    font-family:Figtree,sans-serif;
    text-align: right;
    line-height: 2.5rem;
    margin-right: 0;
    margin-top: 6rem;
    margin-bottom: 6rem;
    font-size: 2rem;
    letter-spacing: .05rem;
}
.a {
    color: #193b1e;
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin-bottom: 3rem;
    margin-top: 2rem;
    font-family:Figtree,serif;
    letter-spacing: .05rem;
    width: fit-content;
    margin: auto;
    border: 1px solid #193b1e;
    padding: .75rem;
    border-radius: 10px;
    text-decoration: none;
    background: #e4f0df;
    min-height: 48px;
    box-sizing: border-box;
}
.a:hover {
    color: #ce4760;
    transform: scale(1.05);
    box-shadow: 0 0 10px #000;
}
.a2 {
    color: #193b1e;
    font-family:Figtree,sans-serif;
    height: 48px;
    min-width: 111px;
    max-width: 200px;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: .5rem;
    text-decoration: none;
    border-radius: 10px;
    background: #e4f0df;
    border: #193b1e 1px solid;
    text-align: center;
}
.a2:hover {
    color: #ce4760;
    transform: scale(1.05);
    box-shadow: 0 0 10px #000;
}
.divider {
    margin: 1.5rem 0;
}
.signature {
    font-family:'Great Vibes',cursive;
    margin-bottom: -6rem;
}
.submission {
    color: #572cb5;
    text-decoration: none;
}
.submission:hover {
    color: #ce4760;
}
@media only screen and (max-width:800px) and (orientation:portrait){
    .img {
        height: 125px;
        width: 125px;
        margin-left: 3rem;
    }
    .myHeadHurts {
        margin-left: 2%;
    }
    .titleContainer {
        display: revert;
    }
    .blockquote {
        border: none;
        margin-top: 2rem;
    }
    .a2 {
        margin-left: 6rem;
    }
}