.hidden-nav-menu {
    position: absolute;
    top: 0;
    right: 0;
    background: url(../public/TopBG.jpg), linear-gradient(to top, rgb(25,59,30,1) -35%,  rgb(228,240,223,1) 15%);
    padding: 1rem;
    border-radius: 0 0 0 25px;
    background-blend-mode: lighten;
    box-shadow: 5px 5px 25px #000;
}
.close-button {
    position: absolute;
    top: 0;
    right: 0;
    margin: 0;
    padding: 0.5rem;
    background-color: transparent;
    border: none;
    font-size: 1.2rem;
    cursor: pointer;
    color: #193b1e;
}
.close-button:hover {
    color: #ce4760;
}
.Navmenu li {
    padding: .75rem;
    text-align: center;
}
.Navmenu {
    display: flex;
    flex-direction: column;
    list-style: none;
    padding-inline-start: 0;
}
.Navmenu a {
    color: #193b1e;
    font-family:'Figtree',sans-serif;
    letter-spacing: .1rem;
    text-decoration: none;
    font-weight: bold;
}
.Navmenu a:hover {
    margin-left: .5rem;
    transform:scale(1.2);
    color: #ce4760;
}
.Menup {
    color: #e4f0df;
    font-size: 2rem;
    margin: .5rem .25rem 0 0;
    font-family:'Indie Flower', cursive;
    letter-spacing: .3rem;
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-decoration: none;
}
.Menup:hover {
    margin-right: 1rem;
    transform:scale(1.2);
    color: #ce4760;
}
.identity {
    color: #ce4760;
    font-family:prata,cursive;
    font-size: 2rem;
    letter-spacing: .2rem;
    margin: 0;
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: center;
}
.lower {
    color: #193b1e;
    font-family:'Great Vibes',cursive;
    font-size: 2rem;
    letter-spacing: .25rem;
    margin: 0;
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    margin-bottom: -.75rem;
    margin-left: 8rem;
    margin-top: 3rem;
    font-weight: bold;
}
.logo {
    height: 125px;
    width: 125px;
    margin-top: -2rem;
    margin-left: -1rem;
    display: flex;
    justify-content: center;
    flex-direction: column;
    border-radius: 100px;
}
.Container {
    display: flex;
}
