.banner {
    min-height: 35vh;
    background-image: url(../public/logo11.svg);
    background-color: #e4f0df;
    background-blend-mode: overlay;
    padding: .5rem;
    box-sizing: border-box;
    background-position: top;
}
.topContainer {
    background-position: top;
    background-image: url(../public/logo11.svg);
    background-color: #e4f0df;
    background-blend-mode: screen;
    margin: 3%;
    padding: 3%;
    border-radius: 25px;
}
.h1 {
    color: #193b1e;
    font-family:'Prata',serif;
    text-align: left;
    letter-spacing: .3rem;
    margin: 0 .25rem;
    box-sizing: border-box;
    border-bottom: 2px #193b1e dashed;
    padding-bottom: 1rem;
}
.h12 {
    color: #193b1e;
    font-family:'Prata',serif;
    text-align: left;
    letter-spacing: .3rem;
    margin: 0 .25rem;
    box-sizing: border-box;
    padding-bottom: 3rem;
}
.p {
    color: #193b1e;
    font-family:Figtree,sans-serif;
    font-size: 1rem;
    letter-spacing: .05rem;
    width: 90%;
    margin: 3rem auto 5rem;
    line-height: 2rem;
    text-align: left;
    font-weight: bold;
}
.h2 {
    color: #193b1e;
    font-family:Pata,serif;
    text-align: center;
    font-size: 2rem;
}
.a {
    color: #572cb5;
    text-decoration: none;
}
.a:hover {
    color: #ce4760;
}
.em {
    color: #ce4760;
    text-decoration: underline;
    text-underline-offset: 2px;
}
.frameContainer {
    display: flex;
    justify-content: center;
    margin-top: 2rem;
}