.banner {
    min-height: 35vh;
    background-image: url(../public/logo11.svg);
    background-color: #e4f0df;
    background-blend-mode: overlay;
    padding: .5rem;
    box-sizing: border-box;
    background-position: top;
}
.h1 {
    color: #e4f0df;
    font-family:'Great vibes',cursive;
    text-align: left;
    letter-spacing: .5rem;
    margin: 0;
    box-sizing: border-box;
    border-bottom: 4px #e4f0df double;
    padding-bottom: 1rem;
}
.givesBack {
    margin: 3%;
    padding: 2%;
    border-radius: 15px;
}
.topP {
    color: #e4f0df;
    text-align: left;
    font-family:Figtree,sans-serif;
    line-height: 1.7rem;
    margin: 1rem 0 0;
    font-size: 1.1rem;
}
.aboutUs {
    background-position: top;
    background-image: url(../public/logo11.svg);
    background-color: #e4f0df;
    background-blend-mode: screen;
    margin: 3%;
    padding: 3%;
    border-radius: 25px;
}
.aboutContaierL {
    display: flex;
    justify-content: flex-start;
}
.aboutContainerR {
    display: flex;
    justify-content: flex-end;
}
.h2 {
    color: #193b1e;
    font-family:Prata,serif;
    letter-spacing: .1rem;
    font-size: 2rem;
    text-align: left;
    border-bottom: dashed 1px #193b1e;
    padding-bottom: 2rem;
    margin-top: 0;
}
.imgContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.imgLeft {
    height: 45vw;
    aspect-ratio: 4/5;
    border-radius: 25px;
    margin-right: 5rem;
}
.imgRight {
    aspect-ratio: 1/1;
    height: 35vw;
    border-radius: 25px;
    margin-left: 5rem;
}
.textContainerR {
    display: flex;
    justify-content: center;
    flex-direction: column;
}
.h3 {
    text-align: right;
    color: #193b1e;
    font-family:'Indie Flower',cursive;
    letter-spacing: .1rem;
    font-size: 2rem;
}
.h4 {
    text-align: right;
    color: #ce4760;
    font-family:'Great Vibes',cursive;
    letter-spacing: .2rem;
    font-size: 1.5rem;
    margin-top: 0;
}
.bottomh3 {
    text-align: right;
    color: #193b1e;
    font-family:'Indie Flower',cursive;
    letter-spacing: .1rem;
    font-size: 2rem;
    margin-bottom: 1rem;
}
.aboutP {
    text-align: left;
    font-family:Figtree,sans-serif;
    letter-spacing: .1rem;
    line-height: 1.55rem;
    color: #193b1e;
    font-weight: bold;
}
.lowerH1 {
    color: #193b1e;
    font-family:Prata,serif;
    width: fit-content;
    margin: 0 auto 2rem;
    letter-spacing: .1rem;
    text-align: center;
}
.lowerH1:hover {
    transform: scale(1.1);
}
.storeLink {
    text-decoration: none;
    color: #572cb5;
    font-size: 2.5rem;
}
.accountLink {
    text-decoration: none;
    color: #572cb5;
}
.accountLink:hover {
    color: #ce4760;
}
.storeLink:hover {
    color: #ce4760;
}
.topLinks {
    color: #e4f0df;
    text-underline-offset: 2.25px;
}
.topLinks:hover {
    color: #ce4760;
}
.bottomLinks {
    color: #572cb5;
    text-decoration: none;
}
.bottomLinks:hover {
    color: #ce4760;
}
.storeP {
    color: #193b1e;
    font-family:Figtree,sans-serif;
    text-align: center;
    font-size: 1.25rem;
    margin-bottom: 2rem;
    font-weight: bold;
    letter-spacing: .1rem;
    line-height: 1.55rem;
    font-size: 1.3rem;
}
.carouselContainer {
    display: flex;
    margin-top: 3rem;
}
.storeButton {
    text-decoration: none;
    color: #e4f0df;
    width: fit-content;
    margin: 0 auto;
    padding: 1rem 1.75rem;
    border-radius: 10px;
    background-color: #193b1e;
    display: flex;
    background-blend-mode: multiply;
    letter-spacing: .15rem;
    background-position: top;
}
.storeButton:hover {
    transform: scale(1.1);
    background-image: url(../public/logo11.svg);
}
@media only screen and (max-width:825px){
    .aboutContaierL {
        flex-direction: column;
    }
    .aboutContainerR {
        flex-direction: column;
    }
    .imgLeft {
        width: 35vw;
        margin: auto;
    }
    .imgRight {
        width: 35vw;
        margin: auto;
    }
    .aboutP {
        text-align: center;
    }
    .h3 {
        text-align: center;
    }
    .bottomh3 {
        text-align: center;
    }
    .h2 {
        text-align: center;
    }
    .carouselContainer {
        flex-direction: column;
    }
    .h4 {
        text-align: center;
    }
}
@media only screen and (min-width:2300px){
    .aboutP {
        font-size: 1.75rem;
        line-height: 3rem;
    }
    .h3 {
        font-size: 3rem;
    }
    .bottomh3 {
        font-size: 3rem;
    }
    .h4 {
        font-size: 2rem;
    }
    .h1 {
        font-size: 3rem;
    }
    .h2 {
        font-size: 3rem;
    }
    .topP {
        font-size: 1.75rem;
        line-height: 3rem;
    }
    .storeP {
        font-size: 1.75rem;
        line-height: 3rem;
    }
}