.carousel {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30%;
    margin: 0 auto;
    position: relative;
}
  
.carousel img {
    max-width: 100%;
    height: auto;
    margin: 0 3rem;
    border-radius: 15px;
    aspect-ratio: 1/1;
}
.text {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    text-align: center;
    background-color: rgba(255, 255, 255, 0.4);
    width: 100%;
    border-radius: 0 0 1rem 1rem;
    padding: 1rem;
}
.carousela {
    color: #193b1e;
    font-family:Figtree,sans-serif;
    font-weight: bold;
    font-size: 1.25rem;
    margin: 0;
    text-decoration: none;
    text-transform: uppercase;
    text-shadow: #e4f0df 5px 5px 4px;
    letter-spacing: .1rem;
}
.carousela:hover {
    color: #ce4760;
    text-shadow: #000 5px 5px 5px;
}
@media only screen and (max-width:825px){
    .carousel img {
        max-width: 300px;
        margin-bottom: 1rem;
    }
    .text {
        margin-bottom: 1rem;
        width: 290%;
    }
}