.call {
    color: #e4f0df;
    margin: 0 0 .5rem 1rem;
    font-family:'Indie Flower',cursive;
}
.header {
    background:  #193b1e;
    margin: 0;
    display: flex;
    justify-content: space-between;
}
.logo {
    height: 150px;
    width: 150px;
    border-radius: 75px;
    margin: 1rem 0 0 1rem;
}
.left {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.right {
    display: flex;
    justify-content: flex-end;
}
.menu {
    font-size: 3rem;
    display: flex;
    justify-content: center;
    flex-direction: column;
}
.Tmenu {
    list-style: none;
    padding-inline-start: 0;
    display: flex;
    margin: 0 .5rem 1rem 0;
    justify-content: flex-end;
}
.Tmenu li {
    padding: .5rem;
}
.socials {
    height: 58px;
    width: 58px;
}
.socials:hover {
    transform: scale(1.2);
}
.notification {
    background: #193b1e;
    margin: 0 0 .5rem;
    border-radius: 25px;
    width: 35%;
}
.notificationtray {
    display: flex;
    justify-content: flex-end;
}
.p {
    color: #193b1e;
    text-align: right;
    margin: 0 .25rem;
    color: #e4f0df;
    font-family:Figtree,sans-serif;
    letter-spacing: .1rem;
    line-height: 1.5rem;
}
@media only screen and (max-width:425px) and (orientation:portrait){
   .Tmenu {
        justify-content: flex-end;
    }
    .socials {
        height: 48px;
        width: 48px;
    }
}
@media only screen and (max-width:1000px){
    .notification {
        width: revert;
    }
}